import React, { Component } from 'react'
import Card from '../Fontcard'
import { getTemplateData } from '../../repository'

class Cards extends Component {
  constructor(props) {
    super(props);
    this.state = {
      bundles: {},
      families: {},
      specials: {}
    }
  }
  componentWillMount() {
    //fetch bundles urls
    var bundlesData = getTemplateData('Bundles');
    this.setState({ bundles_urls: bundlesData[0]["urls"] });
    this.setState({ bundles_cols: bundlesData[0]["cols"] });

    //fetch family urls
    var familiesData = getTemplateData('Family');
    this.setState({ family_urls: familiesData[0]["urls"] });
    this.setState({ family_cols: familiesData[0]["cols"] });

    //fetch specials urls
    var specialsData = getTemplateData('Specials');
    this.setState({ special_urls: specialsData[0]["urls"] });
    this.setState({ special_cols: specialsData[0]["cols"] });
  }

  showTable(cols, urlList, entityType) {
    var noOfcolumns = 0;
    noOfcolumns = cols - 1;
    const tableRows = [];


    for (let i = 0; i < urlList.length; i = i + noOfcolumns) {
      if (cols === 3) {
        tableRows.push(
          <tr key={urlList[i] + "-" + urlList[i + 1] + "-" + i}>

            <td style={{ border: "0px" }}> 
             <Card productUrl={urlList[i]} category={entityType} /></td>
            <td style={{ border: "0px" }}>

              <Card productUrl={urlList[i + 1]} />
            </td>
            <td style={{ border: "0px" }}>

              <Card productUrl={urlList[i + 2]} />
            </td>

          </tr>
        );
      }
      else if (cols === 2) {
        tableRows.push(
          <tr key={urlList[i] + "-" + urlList[i + 1] + "-" + i}>
            <td style={{ border: "0px" }}> 
              <Card productUrl={urlList[i]} category={entityType} />
            </td>
            <td style={{ border: "0px" }}>

              <Card productUrl={urlList[i + 1]} />
            </td>


          </tr>
        );
      }

    }
    return tableRows;
  }


  renderBundles() {
    const { bundles_urls, bundles_cols } = this.state;
    const elements = {bundles_urls}
    const items = []
    for (const [index, value] of elements.bundles_urls.entries()) {
      items.push(<Card productUrl={value} category={'Bundles'} />)
    }
    return (
      <div class="col col-2" >
        {items}
      </div>
    )
  }

  // renderBundles() {
  //   const { bundles_urls, bundles_cols } = this.state;
  //   return (
  //       <table style={{ border: "0px" }}>
  //         <tbody>{this.showTable(bundles_cols, bundles_urls, 'Bundles')}</tbody>
  //       </table>
  //     );
    
  // }
  
  renderFamilies() {
    
    const { family_urls, family_cols } = this.state;
    const elements = {family_urls}
    const items = []
    for (const [index, value] of elements.family_urls.entries()) {
      items.push(<Card productUrl={value} category={'Families'} />)
    }
    return (
      <div className="col col-3" >
        {items}
      </div>
    )
  }


  
  renderSpecials() {
    const { special_urls, special_cols } = this.state;
    return(
      <table style={{ border: "0px solid black " }}>
      {/* <tbody>{this.showTable(special_urls, special_cols, 'Specials')}</tbody> */}
    </table>
    )
  }
  render() {
    return (
      <div className="wrapper">

        <div className="container">
          <h1 className="heading-large">Shop families!</h1>
          {this.renderFamilies()}
        </div>

        <div className="container">
          <h1 align='center'>Shop Bundles!</h1>
          {this.renderBundles()}
        </div>
        
        <div className="container">
          <h1 align='center'>Under $15</h1>
          {this.renderSpecials()}
        </div>
        
      </div>/* wrapper ended here */
    );
  }
}

export default Cards
