import React, { Component } from 'react'
import Axios from 'axios';
import {parseData, parseFamilies} from './dataParser'

class Card extends Component {

  constructor(props) {
    super(props);
    this.state = {
      imageLoaded: false,
      productDetails: []
    }
    this.onImageLoaded = this.onImageLoaded.bind(this);
  }
   /**
   * Callback executed when the image has finished loading.
   */
  onImageLoaded() {
    this.setState({
      imageLoaded: true,
    });
  }
  /**
   * renderImage
   * Renders the promo hero image of the card.
   * @returns {ReactElement}
   */
  renderImage() {
    // Do not display the image while loading.
    const imageStyle = !this.state.imageLoaded ? { display: 'none' } : {};
    const { productDetails, category } = this.state;
    console.log('amit -->', productDetails, category);

    var productUrl = productDetails["productUrl"];
    if(category === 'Bundles'){
      productUrl = 'https://www.myfonts.com' + productUrl  ;
    }
   

    return(
      <div>
                <a  href={productUrl}>
                  <img 
                    src={productDetails.imgSrc} 
                    alt={productDetails["familyName"]}  
                    onLoad={this.onImageLoaded}
                    style={imageStyle} 
                  />
                </a>
      </div>
    )
  }
 

componentWillMount() {
  const { productUrl, category } = this.props;
  //alert(category);

  var new_producturl
    if(category === 'Families'){
      new_producturl = 'https://www.myfonts.com/backbone/fontfamilybyname/' + productUrl.split("fonts/")[1] ;
    } else if(category === 'Bundles'){
      new_producturl = 'https://www.myfonts.com/backbone/package/sku/' + productUrl.split("package/")[1] ;
    }

  Axios.get(new_producturl)
    .then(response => response.data)
    .then(data =>
      this.setState({
       productDetails: parseData(data, category),
       category: category

      })
    )
}
 /**
   * renderExtraInfo
   * Renders the number of styles and foundry if the info is available
   * @returns {ReactElement}
   */
  renderExtraInfo() {
    return (
      <span className='family-type'>
        {this.renderStyles()}
        {this.renderFoundry()}
      </span>
    );
  }
/**
   * renderFoundry
   * Renders the foundry info if available.
   * @returns {ReactElement}
   */
  renderFoundry() {
    const { productDetails, category } = this.state;
    return productDetails["foundryName"] ? <span> by <a href={productDetails["foundryUrl"]}>{productDetails["foundryName"]}</a></span> : null;
  }
  /**
   * renderStyles
   * Renders the number of styles if available.
   * @returns {ReactElement}
   */
  renderStyles() {
    const { productDetails, category } = this.state;
    return this.state.productDetails ? <span># {productDetails["stylesCount"]} Styles</span> : null;
  }
  /**
   * Render the prices in the cards. If the price information is not yet available,
   * then a spinner is rendered (controlled by a prop called showPriceSpinner).
   * @returns {ReactElement}
   */
  renderPrice() {
    const { productDetails, category } = this.state;
    let dataToRender = null;
    const price = productDetails["price"];
    const currentPrice = productDetails["currentPrice"];
    const currency = productDetails["currencyFormat"];
   
    // If the price information is available, show the price instead of the spinner.
    if (price) {
      // Check if the price has a discount.
      
      const discountPrice = price !== currentPrice ? currentPrice : null;
      const priceClassName = discountPrice ? 'original-price' : 'discount-price';
      // Render the price info.
      dataToRender = (
        <div className='prices'>
          <span className={priceClassName}>{currency}{price}</span>
          <span className='discount-price'> {discountPrice==null ? discountPrice : currency+discountPrice}</span>
        </div>
      );
    }
    return dataToRender;
  }

renderDetails() {
  const { productDetails, category } = this.state;
  return (
    <div className="actionbar">
    <div>

      <h2 className="family-heading">{productDetails["productName"]} </h2>
      {this.renderExtraInfo()}
      {this.renderPrice()}
    </div>

    <div className="cart-button">
      <button> Add to cart</button>
    </div>
  </div>
  )
}
render() {
  return (
    <div className="fontcard">
      <div className="thumbnail">
        {this.renderImage()}
      </div>
      {this.renderDetails()}
    </div>
  )
}
}

export default Card