import React, { Component } from 'react'
import Cards from './components/Cards'

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      cmsData: {}
    }
  }

  componentWillMount() {
    console.log('Data from CMS : ', this.props.cmsData)
  }

  render() {
    return (
      <div className="container">
        <div> 
        <h1 align="center">Promotional Offers</h1>
        <Cards/> 
        </div>

      </div>
    )
  }
}

export default App
