export function parseData(data, category) {
  if (category === 'Bundles') {
    return parseBundles(data, category);
  }
  else if (category === 'Families') {
    return parseFamilies(data, category);
  }
  // if (category == 'Specials') {
  //   return this.parseSpecials(data);
  // }

}
export function parseFamilies(data) {
  
  var familiesData = JSON.stringify(data["family"]);
  var response = {};
  var currency = JSON.stringify(data["currencyFormat"]);
  if(familiesData !== undefined){
    var foundry =JSON.parse(familiesData)["foundry"];
    var image =JSON.parse(familiesData)["posters"];
    var licenses = JSON.parse(familiesData)["licenses"];
    var desktopLicense = licenses.filter(c=>c.licenseType===1);
    //alert(JSON.stringify(desktopLicense));
    const price = JSON.stringify(desktopLicense[0]["price"]);
    const currentPrice = JSON.stringify(desktopLicense[0]["currentPrice"]);
    //alert(image[0]["url"]);
    response = {
      "imgSrc": image[0]["url"],
      "foundryId": foundry["foundryID"], 
      "foundryName": foundry["foundryName"], 
      "foundryUrl": "https://myfonts.com" +foundry["foundryURL"],
      "familyID": JSON.parse(familiesData)["familyID"],
      "productName":JSON.parse(familiesData)["familyName"],
      "productUrl": "https://myfonts.com/" + JSON.parse(familiesData)["familyURL"],
      "stylesCount" : JSON.parse(familiesData)["stylesCount"],
      "currencyFormat":  JSON.parse(currency)["currency_symbol"] ,
      "price": price,
      "currentPrice": currentPrice
  }
  //alert( "'https://myfonts.com" + JSON.parse(familiesData)["familyURL"]+"'");
  }
  return response;
}
export function parseBundles(data) {
  
  var bundlesData = JSON.stringify(data["skuData"]);
  var response = {};
  var currency = JSON.stringify(data["currencyFormat"]);
  var noOfStylesInPakage =0;
  
  if(bundlesData!=undefined){
   // console.log('bundlesdata',JSON.parse(bundlesData)["packages"]["0"]);
  
    if(JSON.parse(bundlesData)["packages"]!=undefined || JSON.parse(bundlesData)["packages"]!=null){
      var packages = JSON.parse(bundlesData)["packages"];
      var styles = JSON.parse(JSON.stringify(packages[0]["cartSamples"]));
     
      var foundry =JSON.parse(bundlesData)["foundry"];
       response = {
        "imgSrc": "https:\/\/cdn.myfonts.net\/s\/aw\/720x360\/702\/1\/359494.png", //image[0]["url"],
        "foundryId": foundry["foundryID"], 
        "foundryName": foundry["foundryName"], 
        "foundryUrl": foundry["foundryURL"],
        "familyID": JSON.parse(bundlesData)["skuID"],
        "productName":JSON.parse(bundlesData)["skuName"],
        "productUrl":JSON.parse(bundlesData)["skuURL"],
        "stylesCount" : styles.length,
        "currencyFormat":  JSON.parse(currency)["currency_symbol"] ,
        "price": JSON.parse(bundlesData)["skuPrice"],
        "currentPrice": JSON.parse(bundlesData)["skuPrice"]
    }
    //alert(JSON.stringify(response));
    }
  }

  return response;

}

export function parseSpecials(data){
var specialsData = data["familyData"];
var currency = JSON.parse(data["currencyFormat"])["currency_symbol"];
return specialsData;
} 